// SearchManager.js - 
import { useEffect } from 'react';

// 
const SearchManager = ({
  searchQuery,
  setSearchQuery,
  files,
  currentFileIndex,
  processedData,
  setSelectedMessageIndices,
  setCurrentFileIndex,
  setIsSearching,
  setShowSearchPanel,
  contentSearchQuery,    // : 
  setContentSearchQuery  // : 
}) => {
  // 
  useEffect(() => {
    // window.searchManager
    if (window.searchManager) return;
    
    // 
    window.searchManager = {
      // 
      state: {
        query: '',
        results: [],
        totalCount: 0,
        currentIndex: -1,
        displayIndex: 0,
        isSearching: false
      },
      
      // 
      getState() {
        return {...this.state};
      },
      
      // 
      search(query) {
        if (!query || typeof query !== 'string') return;
        
        this.state.query = query;
        this.state.isSearching = true;
        this.state.currentIndex = -1;
        this.state.displayIndex = 0;
        
        // UI
        this._notifyStateChange();
        
        // setTimeoutUI
        setTimeout(() => {
          this._performSearch();
        }, 10);
      },
      
      // 
      _performSearch() {
        const query = this.state.query.toLowerCase();
        const results = [];
        
        // 
        if (processedData && processedData.chat_history) {
          processedData.chat_history.forEach(msg => {
            if (msg.display_text && msg.display_text.toLowerCase().includes(query)) {
              results.push({
                type: 'local',
                fileIndex: currentFileIndex,
                messageIndex: msg.index,
                preview: msg.display_text.substring(0, 100)
              });
            }
          });
        }
        
        this.state.results = results;
        this.state.totalCount = results.length;
        this.state.isSearching = false;
        
        // 
        if (results.length > 0) {
          this.goToResult(0);
        } else {
          // UI
          this._notifyStateChange();
          
          // 
          setShowSearchPanel(true);
        }
      },
      
      // 
      goToResult(index) {
        if (!this.state.results || this.state.results.length === 0) return;
        
        // 
        if (index < 0) index = 0;
        if (index >= this.state.results.length) index = this.state.results.length - 1;
        
        const result = this.state.results[index];
        this.state.currentIndex = index;
        this.state.displayIndex = index + 1;
        
        // UI
        this._notifyStateChange({currentResult: result});
        
        // 
        if (result.fileIndex !== currentFileIndex) {
          // 
          setIsSearching(true);
          
          // 
          setCurrentFileIndex(result.fileIndex);
          
          // 
          setTimeout(() => {
            setSelectedMessageIndices([result.messageIndex]);
            setIsSearching(false);
          }, 300);
        } else {
          // 
          setSelectedMessageIndices([result.messageIndex]);
        }
      },
      
      // 
      nextResult() {
        const nextIndex = this.state.currentIndex + 1;
        if (nextIndex < this.state.results.length) {
          this.goToResult(nextIndex);
        } else {
          // 
          this.goToResult(0);
        }
      },
      
      // 
      prevResult() {
        const prevIndex = this.state.currentIndex - 1;
        if (prevIndex >= 0) {
          this.goToResult(prevIndex);
        } else {
          // 
          this.goToResult(this.state.results.length - 1);
        }
      },
      
      // 
      _notifyStateChange(additionalData = {}) {
        const event = new CustomEvent('search-status-update', {
          detail: {
            query: this.state.query,
            totalCount: this.state.totalCount,
            currentIndex: this.state.currentIndex,
            displayIndex: this.state.displayIndex,
            isSearching: this.state.isSearching,
            ...additionalData
          }
        });
        window.dispatchEvent(event);
      },
      
      // : 
      setContentSearch(query) {
        setContentSearchQuery(query);
      },
      
      // : 
      clearContentSearch() {
        setContentSearchQuery('');
      }
    };
    
    // 
    const handleSearchNavigation = (e) => {
      if (!window.searchManager) return;
      
      const { action, query } = e.detail;
      
      switch (action) {
        case 'search':
          window.searchManager.search(query || searchQuery);
          break;
        case 'next':
          window.searchManager.nextResult();
          break;
        case 'prev':
          window.searchManager.prevResult();
          break;
        case 'open-panel':
          setShowSearchPanel(true);
          break;
        // : 
        case 'content-search':
          window.searchManager.setContentSearch(query);
          break;
        case 'clear-content-search':
          window.searchManager.clearContentSearch();
          break;
        default:
          break;
      }
    };
    
    window.addEventListener('search-navigation', handleSearchNavigation);
    
    return () => {
      window.removeEventListener('search-navigation', handleSearchNavigation);
    };
  }, [
    searchQuery, 
    files, 
    currentFileIndex, 
    processedData, 
    setSelectedMessageIndices, 
    setCurrentFileIndex, 
    setIsSearching,
    setShowSearchPanel,
    setContentSearchQuery  // 
  ]);
  
  // 
  useEffect(() => {
    if (window.searchManager && searchQuery) {
      // 
      if (window.searchManager.state.query !== searchQuery) {
        window.searchManager.search(searchQuery);
      }
    }
  }, [searchQuery]);
  
  // null
  return null;
};

export default SearchManager;